<template>
  <div id="app">
    <a-input placeholder="" class="my_ipt" :value="inputValue" @change="handleInputChange"></a-input>
    <a-button type="primary" @click="addItemToList"></a-button>

    <a-list bordered :dataSource="infoList" class="dt_list">
      <a-list-item slot="renderItem" slot-scope="item">
        <!-- -->
        <a-checkbox :checked="item.done" @change="(e)=> {cbStatusChanged(e,item.id)}">{{item.info}}</a-checkbox>
        <!-- -->
        <a slot="actions" @click="removeItemById(item.id)"></a>
      </a-list-item>

      <!-- footer-->
      <div slot="footer" class="footer">
          <!-- -->
          <span>{{unDoneLength}}</span>
          <!-- -->
          <a-button-group>
              <a-button :type="viewKey === 'all' ? 'primary' : 'default'" @click="changeList('all')"></a-button>
              <a-button :type="viewKey === 'undone' ? 'primary' : 'default'" @click="changeList('undone')"></a-button>
              <a-button :type="viewKey === 'done' ? 'primary' : 'default'" @click="changeList('done')"></a-button>
          </a-button-group>
          <!-- -->
          <a @click="clean"></a>
      </div>
    </a-list>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapGetters} from 'vuex'
export default {
  name: 'App',
  data() {
    return {
    }
  },
  created() {
    this.$store.dispatch('getList')
  },
  computed: {
    ...mapState(['list','inputValue','viewKey']),
    ...mapGetters(['unDoneLength','infoList'])
  },
  methods: {
    // 
    handleInputChange(e) {
      // console.log(e.target.value)
      this.$store.commit('setInput',e.target.value)
      // console.log(this.inputValue)
    },
    //  item 
    addItemToList() {
        if(this.inputValue.trim().length <= 0 ){
          return this.$message.warning('')
        }

        this.$store.commit('addItem')
    },
    //   id 
    removeItemById(id){
      // console.log(id)
      this.$store.commit('removeItem',id)
    },
    // 
    cbStatusChanged(e,id){
      // e.target.checked 
      // console.log(e.target.checked)
      // console.log(id)
      const param = {
        id: id,
        status: e.target.checked
      }

      this.$store.commit('changeStatus',param)
    },
    // 
    clean() {
      this.$store.commit('cleanDone')
    },
    // 
    changeList(key){
      console.log(key)
      this.$store.commit('changeViewKey',key)
    }
  },
}
</script>

<style scoped>
#app {
  padding:10px;
}

.my_ipt{
  width: 500px;
  margin-right: 10px;
}

.dt_list{
  width: 500px;
  margin-top: 10px;
}

.footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>

